const assumeApproval = () => Promise.resolve(true);
export const AssumeApprovalForEveryAction = {
    obtainApprovalToUpdateAsset: assumeApproval,
    obtainApprovalToSetAssetTags: assumeApproval,
    obtainApprovalToSetAssetCollections: assumeApproval,
    obtainApprovalToDeleteAsset: assumeApproval,
    obtainApprovalToReplaceAsset: assumeApproval,
};
export const DefaultApprovalAttainmentStrategyFactory = (deps) => (Object.assign(Object.assign({}, AssumeApprovalForEveryAction), { obtainApprovalToDeleteAsset: ({ asset }) => deps.interaction.confirm({
        title: deps.intl.translate('actions.deleteAsset.confirm.title', 'Delete Asset', [asset.label]),
        message: deps.intl.translate('action.deleteAsset.confirm.message', 'Do you really want to delete the asset ' + asset.label, [asset.label]),
        buttonLabel: deps.intl.translate('actions.deleteAsset.confirm.buttonLabel', 'Yes, proceed with deleting the asset', [asset.label]),
    }) }));
